.button {
    background-color: #FFFFFF;
    border: 1px solid #CECED2;
    opacity: 0.4;
    color: #000000;
    fill: #000000;
    border-radius: 3px;
    padding: 0.5em;
    font-size: 16px;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
    &:hover, &:focus, &:active {
        opacity: 1;
    }
}

.buttonIcon {
    margin-right: 0.5rem;
}

.button.primary {
    background-color: #784CC0;
    color: #FFFFFF;
    fill: #FFFFFF;
    opacity: 1;
    &:hover {
        background-color: #976BE2;
    }
    &:focus, &:active {
        background-color: #5D3999;
    }
}

.button.full {
    width: 100%;
}
