.modalContainer {
    position: fixed;
    z-index: 500;
    width: 100%;
    min-height: 100%;
    overflow: scroll;
    top: 0;
    left: 0;
}

.modalOverlay {
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 0.3;
    background: #000000;
}

.modal {
    position: absolute;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    padding: 16px;
    left: 15%;
    top: 10%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    border-radius: 5px;
}

.closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
