.item {
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
}

.imageContainer {
    display: flex;
    align-items: center;
    padding-top: 56%;
    height: 0;
    overflow: hidden;
    border-radius: 3px;
}

.image {
    margin-top: -56%;
    min-height: 100%;
    min-width: 100%;
}

.subTitle {
    font-size: 0.75rem;
    color: #000000;
    font-weight: 400;
    letter-spacing: 0.09px;
    line-height: 1.6;
    opacity: 0.6000000238418579;
    justify-content: space-between;
    align-items: flex-start;
}

.location {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.date {
    flex-shrink: 0;
}

.title {
    margin: 0;
    font-size: 1.375rem;
    letter-spacing: -0.58px;
    line-height: 29px;
}

.description {
    word-wrap: break-word;
    width: 100%;
    flex-grow: 1;
}

.cta {
    margin-top: 0.5em;
}